.s-free-ticket {
	padding: clamp(50px, 5vw, 100px) 0;

	.section {
		&__title {
			line-height: 0.9;
			font-weight: 900;
			font-size: clamp(42px, 8vw, 94px);
			text-transform: uppercase;
			margin-bottom: clamp(32px, 8vw, 80px); } } }

.free-ticket {

	&__content {
		display: flex;
		max-width: 824px;
		margin: 0 auto;
		gap: 20px 76px;

		@include sm {
			flex-direction: column-reverse; } }

	&__text {
		display: flex;
		flex-direction: column;
		flex: 1 1 385px;
		justify-content: center;

		@include sm {
			flex: 1 1 auto; }

		.btn {
			margin-top: 30px; } }

	&__descr {
		font-size: clamp(14px, 4vw, 16px);
		line-height: 1.5;

		p {
			&:not(:first-child) {
				margin: 24px 0 0; } } }

	&__img {
		flex: 0 0 363px;

		// +md
		flex: 0 0 300px;
		max-width: 300px;

		@include sm {
			flex: 0 0 auto;
			margin: 0 auto; }

		@include xs {
			max-width: 232px; }

		img {
			display: block;
			width: 100%;
			height: auto; } }

	&__form {
		display: flex;
		max-width: 974px;
		margin: 0 auto;

		@include md {
			flex-direction: column;
			max-width: 310px; } }

	&__input {
		@include regular;
		min-height: 52px;
		line-height: 1.24;
		flex: 1 1 auto;
		background: $white;
		padding: 14px 20px;
		font-size: 14px;
		display: block;

		&::placeholder {
			color: $black; }

		&:not(:first-child) {
			margin: 0 0 0 3px; }

		&:first-of-type {
			border-radius: 26px 0 0 26px; }

		&:last-of-type {
			border-radius: 0 26px 26px 0; }

		@include md {
			border-radius: 26px;

			&:not(:first-child) {
				margin: 10px 0 0 0; }

			&:first-of-type,
			&:last-of-type {
				border-radius: 26px; } } }

	&__submit {
		flex: 0 0 180px;
		margin: 0 0 0 12px;
		min-height: 52px;

		&:after,
		&:before {
			display: none; }

		@include md {
			margin: 30px auto 0;
			flex: 0 0 auto;
			mix-width: 180px; } } }

.badge-img {
	position: relative;

	&:after {
		content: '';
		position: absolute;
		right: 120px;
		top: -172px;
		height: 200px;
		width: 42px;
		background: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 100%);
		z-index: -1;
		transform: skew(-32deg);
		border-radius: 0 0 2px 2px;

		@include sm {
			width: 24px;
			height: 124px;
			right: 66px;
			top: -108px;
			border-radius: 0 0 1px 1px; } }

	&__card {
		display: flex;
		align-items: center;
		position: relative;
		width: 404px;
		height: 228px;
		padding: 84px 52px 40px;
		background: linear-gradient(131.33deg, rgba(255, 255, 255, 0.4) 15%, rgba(255, 255, 255, 0.1) 70%);
		box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.06);
		backdrop-filter: blur(40px);
		border-radius: 16px;
		border: 1px solid rgba(255, 255, 255, 0.2);

		@include sm {
			width: 234px;
			height: 132px;
			padding-left: 62px;
			border-radius: 10px;
			padding: 64px 30px 30px; } }

	&__img {
		margin: 0 20px 0 0;
		width: 94px;
		height: 94px;
		border-radius: 50%;
		background: rgba(0, 0, 0, 0.2);
		box-shadow: inset -1px -1px 0px rgba(255, 255, 255, 0.2);
		display: flex;
		align-items: center;
		justify-content: center;

		@include sm {
			width: 54px;
			height: 54px;
			margin: 0 12px 0 0; }

		.icon-face {
			width: 46px;
			height: 58px;
			fill: #fff;

			@include sm {
				width: 26px;
				height: 34px; } } }

	&__desc {

		div:first-child {
			@include bold;
			font-size: 28px;
			font-weight: 900;
			text-transform: uppercase;
			line-height: 0.9;
			margin: 0 0 2px;

			@include sm {
				font-size: 16px; } }

		div:last-child {
			font-size: 12px;
			margin: 8px 0 0;
			line-height: 0.9;

			@include sm {
				font-size: 8px;
				margin: 5px 0 0; } } }

