.s-line-up {
	padding: var(--section-y, 100px) 0;
	font-family: GothamPro, sans-serif;
	position: relative;
	z-index: 2;
	.section__title {
		margin: 0 auto 46px;
		text-align: center;
		font-size: clamp(40px, 6.8vw, 80px);
		letter-spacing: 0.01em;
		color: var(--bodyColor);
		@include black;
		line-height: .9;
		text-transform: uppercase;
		@include sm {
			margin-bottom: 30px; } } }
.line-up {
	display: grid;
	grid-template-columns: 120px 1fr;
	gap: 30px;
	align-items: flex-start;
	@include tablet {
		display: block; }

	&__avatar {
		width: 120px;
		height: 120px;
		border-radius: 20px;
		overflow: hidden;
		opacity: 0;
		pointer-events: none;
		transition: opacity .2s ease, left .3s ease;
		transition-delay: 0.08s;
		position: absolute;
		top: 50%;
		margin-top: -80px;
		left: -160px;
		@include tablet {
			position: static;
			opacity: 1;
			width: 64px;
			height: 64px;
			border-radius: 8px;
			margin: 0;
			flex: none;
			pointer-events: auto; }
		@include sm {
			height: 40px;
			width: 40px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; }
		&:after {
			content: '';
			position: absolute;
			inset: 0;
			background: linear-gradient(136deg, color-mix(in srgb, var(--accentColor) 1%, transparent) 54.97%, color-mix(in srgb, var(--accentColor) 60%, transparent) 100%);
			@include tablet {
				display: none; } } }
	&__list {
		min-width: 1px;
		grid-column: 2;

		&-item {
			@include tablet {
				&:nth-child(10) ~ *:not(.line-up__announced) {
					display: none; } }
			@include sm {
				&:nth-child(7) ~ *:not(.line-up__announced) {
					display: none; } } }
		&-link {
			display: flex;
			align-items: flex-start;
			gap: 16px;
			text-decoration: none;
			color: var(--bodyColor);
			position: relative;
			padding: 8px 0;

			@media (any-hover: hover) {
				&:hover, &:focus-visible {
					.line-up__list-talk {
						color: var(--accentColor); } } }
			@include rmin(1201) {
				&:hover, &:focus-visible {
					.line-up__avatar {
						opacity: 1;
						left: -140px; } } }
			@include sm {
				gap: 12px; } }
		&-content {
			align-self: center; }
		&-name {
			font-size: 20px;
			font-weight: 300;
			display: inline;
			color: #828282;
			white-space: nowrap;
			@include sm {
				font-size: 16px; }
			@include xs {
				font-size: 14px; } }
		&-talk {
			font-size: 20px;
			font-weight: 800;
			display: inline;
			@include tablet {
				display: block;
				margin-bottom: 3px; }
			@include sm {
				font-size: 16px; }
			@include xs {
				font-size: 14px; } } } }

.line-up__announced {
	font-size: 20px;
	font-weight: 300;
	margin-top: 16px;
	@include tablet {
		text-align: center; }
	@include sm {
		font-size: 16px; }
	@include xs {
		font-size: 14px; } }
