.footer {

  &__nav {
    margin: 0 0 0 100px;

    @include md {
      margin-left: 0; }

    @include sm {
      width: 100%;
      margin: 0 0 24px; } }

  &__socials {
    @include sm {
      order: 0; }

    .social {
      display: flex;
      gap: 7px;
      flex-wrap: wrap;
      margin: 0 auto;

      &.social--theme {
        .social {
          &__item {
            margin: 0; } } }

      &__item {
        margin: 0; }

      &__link {
        width: clamp(44px, 9vw, 78px);
        height: clamp(44px, 9vw, 78px);
        border: 3px solid $gray2;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        transition: all ease 0.1s;
        background: $mainBg;

        &:hover, &:focus-visible {
          outline: none !important;
          border-color: $themeColor2;

          .icon {
            fill: $themeColor2; } }

        .icon {
          width: 22px;
          height: 22px;
          fill: $themeColor2;

          @include sm {
            width: 16px;
            height: 16px; } } } } }

  &__main {
    padding: 60px 0 80px;
    display: flex;

    width: 100%;
    flex-direction: column;
    @include laptop {
      padding: 40px 0 60px; }
    @include sm {
      padding: 30px 0; } }

  &__title {
    font-size: clamp(42px, 6vw, 94px);
    text-transform: uppercase;
    font-weight: 900;
    margin-bottom: clamp(24px, 5vw, 58px);
    text-align: left; }

  &__mid {
    display: flex;
    gap: 34px 120px;
    @include sm {
      flex-direction: column-reverse; } }

  &__basement {

    margin: 0 auto;
    background-position: top center;
    border-top: 3px solid $gray2;
    padding: 40px 0 80px;

    @include sm {
      padding: 25px 0; }

    .container {
      display: flex;
      align-items: center;
      gap: 40px;

      @include sm {
        flex-direction: column-reverse; } } }

  &__tip {
    line-height: 1.42;
    font-size: 14px;

    a {
      color: #FE5D02; }

    @include sm {
      font-size: 12px; } } }

.f-logo {
  color: $white;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 12px;

  &__label {
    font-weight: 500;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    font-size: 12px;
    margin-right: 0;

    a {
      font-size: 23px;
      color: $white;
      text-decoration: none; } }

  &__icon {
    width: 50px;

    img {
      width: 100%; } } }

.f-nav {
  display: flex;
  flex-direction: column;
  font-size: 16px;

  @include sm {
    font-size: 14px; }

  &__item {
    &:not(:first-child) {
      margin-top: 10px; } }

  &__link {
    color: $white; } }
